<template>
  <div>
    <layout-box one="用户管理" two="用户列表">
      <!-- 搜索添加 -->

      <el-card shadow="never" class="mb-1">
        <div>
          <i class="el-icon-search"></i>
          <span>筛选搜索</span>
          <el-button
            style="float: right"
            type="primary"
            size="small"
            @click="handleSearchList()"
          >
            查询结果
          </el-button>
          <el-button style="float:right;margin-right: 15px" size="small" @click="handleResetSearch">重置</el-button>
        </div>

        <div class="mt-1">
          <el-form :inline="true" size="small" label-width="140px" :model="formInline">
            <el-form-item label="店铺名称">
              <el-input v-model="formInline.shopName" placeholder="输入店铺名称" clearable @clear="fetch"></el-input>
            </el-form-item>
          </el-form>
        </div>
      </el-card>
      <!-- 管理列表 -->
      <el-table border stripe :data="userlist">
        <el-table-column label="账户" prop="loginName" align="center">
        </el-table-column>
        <el-table-column label="店铺名称" prop="shopName" align="center">
        </el-table-column>
        <el-table-column label="手机号" prop="mobile" align="center">
        </el-table-column>
        <el-table-column label="添加时间" prop="createTime" align="center">
        </el-table-column>
      </el-table>
      <el-pagination
        class="mt-1"
        :current-page.sync="queryInfo.pageNo"
        :page-sizes="[2, 5, 10, 20]"
        :page-size="queryInfo.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
      >
      </el-pagination>
    </layout-box>
  </div>
</template>

<script>
const defaultListQuery = {
  pageNo: 1,
  pageSize: 10,
  keyword: null
}
export default {
  data() {
    return {
      // 状态数据列表
      statusOptions: [],
      userType: [
        { value: '0', label: '平台' },
        { value: '1', label: '商家' },
        { value: '2', label: '小超' },
        { value: '3', label: '普通' },
      ],
      // 分页的字段
      queryInfo: Object.assign({}, defaultListQuery),
      enableStatus: null,
      total: 0,
      userlist: [],
      // 当前需要被分配角色的用户
      userId: '',
      no: '2405120000360',
      formInline: {},
    }
  },
  created() {
    this.fetch()
    this.fetuserId()
  },
  methods: {
    // 获取用户的按钮权限
    async fetuserId() {
      await this.$http.get('/woho/sys/user/getPermission')
    },
    // 根据userId查询商户(分页)
    async fetch() {
      let data = this.queryInfo
      Object.assign(data, this.formInline)
      const res = await this.$http.post('/woho/areauser/findShopByUserId', this.queryInfo)
      // console.log(res)
      if (res.data.code !== 200) return
      this.userlist = res.data.data.list
      this.total = res.data.data.total
      this.statusOptions = res.data.data
    },
    handleSizeChange(newSize) {
      this.queryInfo.pageSize = newSize
      this.fetch()
    },
    // 监听 页码值 改变的事件
    handleCurrentChange(newPage) {
      this.queryInfo.pageNo = newPage
      this.fetch()
    },
    // 搜索
    handleSearchList() {
      this.fetch()
    },
    handleResetSearch () {
      this.formInline = {}
    }
  }
}
</script>

<style></style>
